body nav {
  margin-bottom: 10px;
}
body nav img {
  height: 64px;
}
body nav li a.active {
  background-color: rgba(0, 0, 0, 0.1);
}
body .x:before {
  content: "";
  background: url("../img/x-mark.svg") no-repeat;
  position: relative;
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  display: inline-block;
}
body .check:before {
  content: "";
  background: url("../img/checked.svg") no-repeat;
  position: relative;
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  display: inline-block;
}



